import { createStore } from '@stencil/store';
import { Environment, getEnvironment } from '../entities/environment';
import {
  DEFAULT_ENVIRONMENT,
  DEFAULT_LOCALE,
  DEFAULT_UNREAD_MESSAGES_COUNT,
  LOGGED_OUT_USER_STATUS
} from '../utility/constants';
import { ApplicationType, Application } from '../utility/constants/app';

export interface NavState {
  loggedInState: string;
  unreadMessagesCount: number;
  userConsent: boolean;
  env: Environment;
  app: ApplicationType;
  usingKeyboard: boolean;
  locale: string;
}

export const navStateStore = createStore<NavState>({
  loggedInState: LOGGED_OUT_USER_STATUS,
  unreadMessagesCount: DEFAULT_UNREAD_MESSAGES_COUNT,
  userConsent: false,
  env: getEnvironment(DEFAULT_ENVIRONMENT),
  app: Application.default,
  usingKeyboard: false,
  locale: DEFAULT_LOCALE
});
